import React, { Component } from 'react'

class Copyright extends Component {
    render () {
        return (
                <div id="copyright">
					&copy; Untitled. All rights reserved. | Design: <a href="http://html5up.net">HTML5 UP</a>
				</div>
        )
    }
}

export default Copyright


